import { AfterViewInit, Component, OnInit, inject } from '@angular/core';
import { ControlContainer, FormControl } from '@angular/forms';
import { MatIcon } from '@angular/material/icon';
import { MatTooltip } from '@angular/material/tooltip';

@Component({
  selector: 'ui-upload-image',
  templateUrl: './upload-image.component.html',
  styleUrl: './upload-image.component.scss',
  standalone: true,
  imports: [MatTooltip, MatIcon],
})
export class UploadImageComponent implements OnInit {
  private controlContainer = inject(ControlContainer);

  // @Input() profilePicture!: string;
  profilePicture!: any;
  profilePictureControl!: FormControl;
  isImgRemoved = false;

  ngOnInit(): void {
    this.profilePictureControl = this.controlContainer.control?.get(
      'profilePicture',
    ) as FormControl;

    this.profilePicture = this.profilePictureControl.value;
    console.log(this.profilePicture);

    // console.log(this.profilePictureControl);
  }

  handleProfileImg(event: any) {
    const selectedFile = event.target.files[0];
    if (selectedFile) {
      this.profilePictureControl.patchValue(selectedFile);
      this.profilePictureControl.updateValueAndValidity();
      this.isImgRemoved = false;

      const fileReader = new FileReader();
      fileReader.onload = () => {
        this.profilePicture = fileReader.result;
      };

      fileReader.readAsDataURL(selectedFile);
    }
  }

  removeImg() {
    this.isImgRemoved = true;
    this.profilePicture = '';
    this.profilePictureControl.patchValue('');
    this.profilePictureControl.updateValueAndValidity();
  }
}
